@import "../..//../assets/sass/main.scss";

.newsContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    // padding: 40px 112px 40px 106px;

    @media screen and (max-width: 1024px) {
        padding: 40px 36px;
    }

    @include breakpoint(sm) {
        flex-direction: column;
        padding: 40px 34px 0px 14px;
    }


    .mainTitle {
        padding-left: 20px;
        color: #363636;
        font-family: Inter;
        font-size: 32px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        text-transform: capitalize;
    }

    .ourGallery {
        // width: 60%;
        height: 100%;

        @media screen and (max-width: 500px) {
            width: 100%;
        }

        .horizontalContainer {
            display: flex;
            justify-content: space-between;
            align-items: center;

            @media screen and (max-width: 500px) {
                flex-direction: column;
                width: 100%;
                padding: 0;
            }

            .rectangleDiv {
                margin: 10px;
                border-radius: 12px;
                background-color: #f5f7f7;
                height: 310px;
                display: flex;
                padding: 15px;
                flex-direction: column;
                align-self: flex-start;
                justify-content: unset;
                align-items: center;

                @media screen and (max-width: 500px) {
                    width: 100%;
                    height: auto;

                }

                .title {
                    padding-top: 30px;
                    align-self: flex-start;
                    color: #363636;
                    text-align: center;
                    font-family: Inter;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: normal;
                    text-transform: capitalize;
                    margin-bottom: 0;
                }

                .subtitle {
                    padding-top: 10px;
                    align-self: flex-start;
                    color: #666;
                    text-align: center;
                    font-family: Inter;
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    text-transform: capitalize;
                    margin-bottom: 0;
                }
            }


        }
    }

    .ourNews {

        // width: 40%;
        // height: 100%;
        @media screen and (max-width: 500px) {
            width: 90%;

        }

        // border: 1px solid black;
        .grid {
            height: 300px;
            overflow: hidden;
            margin-top: 10px;

            // .row{
            //     border: 1px solid black;
            //     animation: scrollTestimonials 10s linear infinite;
            // }
            .news-detail {
                padding: 10px 0 0 0;
                background-color: $white;
                box-shadow: 0px 0px 5px 2px #ccc;
                margin: 10px 20px 0px 20px;
                // overflow: hidden;
                overflow-y: scroll;
                width: 90%;
                height: 96%;
                align-items: center;

                @media screen and (max-width: 250px) {
                    // flex-direction: column;
                    width: 100%;
                    height: 100%;
                    overflow: hidden;

                }

                .close {
                    cursor: pointer;
                    padding: 0 6px 6px 6px;
                }

                .news-header {
                    align-items: center;
                    justify-content: center;
                    display: flex;

                    h6 {
                        font-size: $f20;
                        line-height: 30px;
                        margin-bottom: 10px;
                        // text-align: justify;
                    }
                }

                .news-body {
                    align-items: flex-start;
                    display: flex;
                    // justify-content: center;
                    flex-direction: row;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    overflow: hidden;
                    margin-left: 30px;


                    p {
                        font-size: 15px;
                        text-align: left;
                        margin-left: 8px;

                    }

                    .modalNewsImg {
                        height: 100%;
                        width: 100%;
                    }
                }

                .viewMore {

                    display: flex;
                    justify-content: flex-end;
                    align-items: flex-end;

                    a {
                        align-self: flex-end;
                        text-align: right;

                    }
                }
            }
        }

        @include breakpoint(sm) {
            width: 100%;
            margin-left: 10px;
            margin-top: 20px;
            padding: 0;
        }

        .vertialContainer {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            gap: 5px;

            @include breakpoint(sm) {
                padding-right: 0;
            }

            .newsCard {
                animation: scrollTestimonials 10s linear infinite;
                width: 100%;
                margin: 10px;
                border-radius: 12px;
                background-color: #f5f7f7;
                height: 105px;
                padding: 10px;
                display: flex;
                flex-direction: row;
                gap: 5px;
                align-items: center;
                cursor: pointer;

                @media screen and (max-width: 500px) {
                    margin: 0;
                    padding: 10px;
                    margin-bottom: 20px;
                }

                .title {
                    color: #363636;
                    width: 100%;
                    font-family: Inter;
                    font-size: 15px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: normal;
                    text-transform: capitalize;
                    margin-bottom: 0;
                    margin-top: 8px;

                    @include breakpoint(md) {
                        font-size: 11px;
                    }

                    @media screen and (max-width: 500px) {
                        // width: 100%;
                        font-size: $f14;
                    }
                }

                .subtitle {
                    color: #666;
                    font-family: Inter;
                    font-size: 12px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: normal;
                    text-transform: capitalize;
                    margin-top: 20px;

                    @media screen and (max-width: 500px) {
                        margin-top: 10px;
                    }
                }

                .newsImage {
                    position: relative;
                    border-radius: 3px;
                    height: 80px;
                    object-fit: cover;
                    margin-right: 10px;
                }

                .date {
                    h6 {
                        font-size: 14px;
                        color: gray;
                        font-weight: normal;
                    }
                }
            }
        }

    }

}

.modal-open .news {
    margin: auto;
    top: 260px;
    left: 370px;
    overflow-y: hidden;

    @media (max-width: 768px) {
        right: 0;
        left: 0;
        margin: auto;
        width: auto;
    }

    .modal-title {
        font-size: 16px;
    }

    .subtitle {
        padding: 0;
        font-size: 16px;
    }

    a {
        text-transform: capitalize;
    }
}

.row.paused {

    animation-play-state: paused;
}

.md-none {
    @media (max-width: 768px) {
        display: none;
    }
}

.md-block {
    display: none;
}