@import "../../assets/sass/main.scss";

.filter-section {
  padding: 0 0 60px 0;

  .row {
    background-color: $white;
    padding: 20px;
    position: relative;
    margin-top: -40px;
    margin-left: 0;
    border-radius: 10px;
    box-shadow: 0px 6px 12px #ccc;
    @include breakpoint(lg) {
      padding: 20px 0;
    }
    @include breakpoint(md) {
      padding: 10px 6px;
    }
    @include breakpoint(sm) {
      padding: 6px 0;
      margin-left: 10px;
      margin-right: 10px !important;
    }
    .col-md-3 {
      @include breakpoint(lg) {
        padding-right: 0;
      }
      @include breakpoint(sm) {
        padding-right: 0;
      }
    }
    .filters {
      border-right: 2px solid #dcdcdc;

      svg {
        color: #b0b0b0;
        margin-right: 14px;
        @include breakpoint(lg) {
          margin-right: 6px;
          height: 20px;
          width: 22px;
        }
        @include breakpoint(sm) {
          margin-right: 4px;
          height: 20px;
          width: 22px;
        }
      }

      h4 {
        font-size: $f20;
        color: #b0b0b0;
        font-weight: normal;
        @include breakpoint(md) {
          font-size: $f14;
        }
        @include breakpoint(sm) {
          font-size: $f16;
        }
      }
    }

    button {
      border: none;
      background-color: #be0e14;
      color: $white;
      padding: 8px 34px;
      border-radius: 4px;
      margin: 4px 0 0 0;
      font-size: $f18;
      @include breakpoint(md) {
        font-size: $f14;
      }
      @include breakpoint(sm) {
        margin-left: -16px;
      }
    }
  }
}

.description-section {
  .head {
    font-weight: bold;

    @include breakpoint(sm) {
      font-size: $f28;
      line-height: 30px;
      margin-bottom: 18px;
    }
  }
}

.careerwraper {
  padding: 40px 0;

  .image {
    background-color: #F6F0FF;
    padding: 15px;
    height: 100%;
    width: 100%;

    img {
      height: 100%;
      width: 100%;
      border-radius: 10px;
    }
  }

  .content {
    padding: 0 0 0 40px;
    width: 100%;

    @include breakpoint(sm) {
      margin-top: 30px;
      padding-left: 0;
    }

    h2 {
      font-size: 36px;
      line-height: 30px;
      margin-bottom: 20px;

      @include breakpoint(sm) {
        font-size: $f28;
      }
    }

    p {
      text-align: justify;
      margin-bottom: 4px;
    }

    a {
      color: $black;
      font-size: $f18;
      font-weight: bold;
    }
  }
}
.award {
  @include breakpoint(sm) {
    display: flex;
    flex-direction: column-reverse;
  }
}
.servicesWrapper {
  h2 {
    font-size: $f42;
  }

  @include breakpoint(sm) {
    h2 {
      font-size: $f28;
    }

    p {
      padding-top: 0 !important;
    }
  }
  .form-check-input{
    background-color:#085B88;
    border-color: #085B88;
}

}

.feature-section {
  padding: 40px 0;

  @include breakpoint(sm) {
    padding-top: 0;
  }

  h2 {
    font-size: 42px;
    margin-bottom: 4px;

    @include breakpoint(sm) {
      font-size: $f28;
      line-height: 34px;
    }
  }

  .fea-card {
    background-color: $white;
    border: 1px solid #e6e6e6;
    border-radius: 4px;
    .fea-hr-line{
      padding: 20px;
    }

    @include breakpoint(sm) {
      margin-bottom: 20px;
    }

    hr {
      width: 100%;
      margin: 0;
    }

    h4 {
      font-size: $f24;
      line-height: 30px;
    }
    p {
      @include breakpoint(md) {
        font-size: $f14;
      }
      @include breakpoint(sm) {
        font-size: $f16;
      }
    }
  }
}

.career-section {
  padding: 40px 0;

  @include breakpoint(sm) {
    padding: 0;
  }

  .row {
    @include breakpoint(sm) {
      display: flex;
      flex-direction: column-reverse;
    }
  }

  h2 {
    font-size: 42px;
    margin-bottom: 4px;

    @include breakpoint(sm) {
      font-size: $f28;
    }
  }

  .info {
    padding-right: 40px;

    @include breakpoint(sm) {
      padding-right: 0;
      margin-top: 20px;
    }

    p {
      text-align: justify;
      margin-bottom: 0;
    }

    button {
      border: none;
      background-color: #e21d23;
      color: $white;
      padding: 8px 90px;
      border-radius: 4px;
      margin: 4px 0 0 0;
      font-size: $f18;
      text-transform: capitalize;
      margin-top: 20px;
    }
  }

  .image {
    background-color: #F6F0FF;
    padding: 15px;
    height: 100%;
    width: 100%;

    img {
      height: 100%;
      width: 100%;
      border-radius: 10px;
    }
  }
}

.team-section {
  padding: 40px 0 60px 0;

  h2 {
    font-size: 42px;
    margin-bottom: 4px;

    @include breakpoint(sm) {
      font-size: $f26;
    }
  }

  .team-card {
    @include breakpoint(sm) {
      margin-bottom: 20px;
    }

    .team-info {
      @include breakpoint(sm) {
        padding: 0 40px;
      }

      p {
        font-size: $f16;
        margin-bottom: 0;
      }

      h4 {
        font-size: $f22;
        line-height: 40px;
        color: #be0e14;
      }
    }
  }
}
.bannerBackground{
  background-color: #F6F0FF;
.bannerwrapper {
  position: relative;
  width: 100%;
  height: auto;
  padding: 50px 0;
  background-image: url(../../../public/images/pages/career/banner.jpg);
  backdrop-filter: blur(4px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @include breakpoint(sm) {
    padding-top: 30px;
  }
  .know-details{
    padding-bottom: 10px;
  }
  .left-info {
    padding-right: 40px;

    @include breakpoint(sm) {
      padding-right: 0;
      text-align: justify;
      margin-bottom: 30px;
    }

    p {
      margin-bottom: 0;
    }

    h2 {
      font-size: 42px;
      margin-bottom: 4px;

      @include breakpoint(sm) {
        font-size: $f26;
      }
    }

    a {
      color: $black;
      font-size: $f18;
      text-transform: capitalize;
      text-decoration: underline !important;

      i {
        margin-left: 10px;
      }
    }
  }

  .right-section {
    background-color: $white;
    padding: 20px;
    width: 100%;
    box-shadow: 0px 0px 6px #ccc;
    border-radius: 20px;
    display: flex;
    align-items: baseline;
    margin-bottom: 26px;
    p {
      height: 140px;
      @include breakpoint(sm) {
        height: auto;
      }
    }
    .sideImage {
      position: absolute;
      left: -20px;
      top: 20%;
    }
    @include breakpoint(sm) {
    }
    
    span {
      background-color: #be0e14;
      padding: 8px 15px;
      border-radius: 20px;
      color: $white;
      margin-right: 20px;
    }

    h4 {
      display: inline;
      font-size: $f24;
      line-height: 20px;
    }

    p {
      margin-bottom: 6px;
    }
  }
}
}

.right::before {
  content: "";
  background-image: url(../../../public/images/pages/career/career1.jpg);
}

.hostImage {
  position: relative;
  .sideLeftImage {
    @include breakpoint(sm) {
      display: none;
    }
    position: absolute;
    left: -67px;
    top: 52%;
    img {
      height: 256px;
    }
  }
  .sideRightImage {
    @include breakpoint(sm) {
      display: none;
    }
    position: absolute;
    right: -67px;
    top: 52%;
    img {
      height: 256px;
    }
  }
}
